<script>
    import $ from "jquery";
    import '@vant/touch-emulator';

    export default {
        data() {
            return {
                current: 0,
                menu: false,
                searchValue: '',
                activeKey: '',
                menuArr: [],
                hotArr: [],
                typeId1Arr: [],
                typeId2Arr: [],
                typeId3Arr: [],
                typeId4Arr: [],
                isLoading: false,
                images: [
                    'icon/刘诗诗1.jpeg',
                    'icon/刘诗诗2.jpeg',
                    'icon/刘诗诗3.jpeg',
                ],
            }
        },
        created() {
            this.getData();
        },
        methods: {
            getData(){
                let _this = this;
                $.ajax({
                    url: _this.baseUrl + "/user/getSysMenu",
                    data: {},
                    type: 'post',
                    traditional: true,
                    async: true,
                    dataType: "json",
                    success: function (data) {
                        _this.menuArr = data.data;
                        $.ajax({
                            url: _this.baseUrl + "/user/getHomeData",
                            data: {},
                            type: 'post',
                            traditional: true,
                            async: true,
                            dataType: "json",
                            success: function (data) {
                                _this.hotArr = data.data.a0;
                                _this.typeId1Arr = data.data.a1;
                                _this.typeId2Arr = data.data.a2;
                                _this.typeId3Arr = data.data.a3;
                                _this.typeId4Arr = data.data.a4;
                                _this.isLoading=false;
                            }
                        });
                    }
                });

            },
            onSearch() {
                let _this=this;
                if(_this.searchValue){
                    this.$router.push({path: '/search', query: {vod_name: _this.searchValue}});
                }else{
                    this.$toast({type:'fail',message:'搜索关键词不能为空'});
                }

            },
            searchType(item) {
                this.$router.push({path: '/type', query: {type_id: item.type_id, type_name: item.type_name}});
            },
            onRefresh() {
                let _this=this;
                setTimeout(function () {
                    _this.getData()
                },2000)

            },
        },
    };
</script>
